<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-10 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
            <p class="text-lg font-semibold p-2">Event Info / Room Info</p>
            <div class="text-right p-3">
            <button @click="$router.push(`/zadmin/kategori/room/galery?id=${vdata.id}`)" v-if="vdata.id" type="button" class="btn btn-sm btn-success  ">
                <img src="https://upload.egov.balangankab.go.id/svg/image.svg" 
                    class="d-inline" style="height:15px;width:15px;filter: brightness(0) invert(1);" alt="">
                    Galery Gambar
                <img src="https://upload.egov.balangankab.go.id/svg/arrow-right.svg" 
                    class="d-inline" style="height:15px;width:15px;filter: brightness(0) invert(1);" alt="">
            </button>
            </div>
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="row">
            <div class="col-6">
              <div class="sm-form ">
                  <label for="title">title</label>
                  <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
              </div>
              <div class="sm-form ">
                  <label for="status">status Item</label>
                  <select class='form-control' v-model="vdata.status">
                      <option>Penuh</option>
                      <option>Kosong</option>
                  </select>
              </div>
              <p class="p-2">Type : </p>
              <select class='form-control' v-model="vdata.type">
                <option>type 1</option>
                <option>type 2</option>
                <option>type 3</option>
              </select>
              <div class="sm-form ">
                  <label for="harga">Harga/Range harga</label>
                  <input type="number" id="harga" name="harga" class="form-control form-control-sm" placeholder="ex: 1000000" v-model="vdata.harga" >
              </div>
              <div class="sm-form ">
                  <label for="diskon">Diskon</label>
                  <input type="number" id="diskon" name="diskon" class="form-control form-control-sm" placeholder="ex : 10" v-model="vdata.diskon" >
              </div>
              <div class="sm-form ">
                  <label for="tag">Tag</label>
                  <input type="text" id="tag" name="tag" class="form-control form-control-sm" placeholder=" example : 2 bed, 2 toilet, etc" v-model="vdata.tag" >
              </div>
              <div class="sm-form ">
                  <label for="dollhouse">Dollhouse</label>
                  <input type="text" id="dollhouse" name="dollhouse" class="form-control form-control-sm" placeholder="link dollhouse matterport and etc" v-model="vdata.dollhouse" >
              </div>
              <div class="sm-form mt-2">
                  <label for="id360">data gambar view 360</label>
                  <select class='form-control' v-model="vdata.id360">
                  <option v-for="(item, index) in datanya2" :key="index+'datnaya2'" :value="item.id">{{item.title}}</option>
                  </select>
              </div>
            </div>
            <div class="col-6">
              <div class="sm-form ">
                  <label for="fasilitas">fasilitas ruangan</label>
                  <div class="sm-form">
                      <textarea type="text" id="fasilitas" name="fasilitas" rows="5" placeholder="fasilitas..." class="form-control md-textarea" v-model="vdata.fasilitas" ></textarea>
                  </div>
              </div>
              <div class="sm-form ">
                  <label for="detail">detail ruangan</label>
                  <div class="sm-form">
                      <textarea type="text" id="detail" name="detail" rows="5" placeholder="detail..." class="form-control md-textarea" v-model="vdata.detail" ></textarea>
                  </div>
              </div>
              
              <div class="sm-form ">
                  <label for="gambar">gambar</label>
                  <input type="file" id="filenya" name="gambar" class="form-control form-control-sm" placeholder="gambar" @change="upload" >
                  <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                  <img :src="vdata.gambar" v-if="vdata.gambar" v-viewer style="height:100px;width:100px;" alt="">
              </div>
            </div>
          </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-10 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['gambar','idpeta','tag','dollhouse','id360']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let urlnya=localStorage.getItem('urlnya');
let sdb = new autophp();
export default {
  layout:'app_admin2',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
      datanya2:[],
    };
  },
  methods: {
      upload(){
          let file = document.querySelector("#filenya"); // berikan id pada input file
          sdb.collection('tbuser',false,urlnya).upload(file,true,true) // file,compress,scramble
          .then(res=>{
              this.vdata.gambar=res;
              this.$forceUpdate();
          })
      },
    getData(){
      sdb.collection("app_gis_ruangan",false,urlnya).doc().select(`select * from app_gis_ruangan where idpeta='${this.$route.query.id}'`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
      sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data where idpeta='${this.$route.query.id}'`).then(res => {
        this.datanya2=res;
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      that.vdata.idpeta=that.$route.query.id
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_gis_ruangan",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_gis_ruangan",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_gis_ruangan",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>